import popupVerify from "@/components/popupVerify.vue"
export default {
	components:{
		popupVerify
	},
	data() {
		return {
			form:{
				
				newPassword:''
			},
			repeatPassword:'',
			phone:'',
			email:''
		};
	},
	onLoad:function(options){
		this.phone = options.phone
		this.email = options.email
		console.log(options)
	},
	methods:{
		confirmInput:function(){
			this.submit()
		},
		showDialog:function(){
		
			this.$refs.dialog.show()
		},
	    async sendVcode(){
			if (this.form.mail == ''){
				uni.showToast({
					title: '郵箱不能為空',
					icon: 'none',
					duration: 1000
				});
				return
			}
			let data ={
				mail: this.form.mail
			}
	    	const res  = await this.$http.get('/user/verification',{data: data})
	    	uni.showToast({
	    		title: '發送成功',
				duration:  1000
	    	});
	    	
	    },
		
		async submit(){
			if (this.form.newPassword !== this.repeatPassword) { 
				uni.showToast({
					title: '兩次密碼不一致',
					icon: 'none'
				});
				return
			}
			uni.showLoading({
				title: '提交中',
				mask: true
			});
			const res = await this.$http.get('/user/update_pay_password',{data: this.form})
			uni.hideLoading()
			console.log(res)
			uni.showToast({
				title: '修改成功',
				icon: 'none',
				duration: 1000
			});
			setTimeout(function() {
				uni.navigateBack({
					delta: 1
				});
			}, 1500);
			
			 
		},
		scanCodeToken:function(){
			uni.scanCode({
			    success: function (res) {
			        console.log('条码类型：' + res.scanType);
			        console.log('条码内容：' + res.result);
			    }
			});
		},
		scanCodeNode: function(){
			uni.scanCode({
			    success: function (res) {
			        console.log('条码类型：' + res.scanType);
			        console.log('条码内容：' + res.result);
			    }
			});
		}
	}
}
